.input {
  width: 100%;
  padding: 18px;
  background-color: var(--main-bg-cl);
  border-radius: 10px;
  border: none;
}

.input::placeholder {
  color: var(--placeholder-color);
}

.textArea {
  width: 100%;
  min-height: 114px;
  resize: none;
}

.text {
  font-weight: 500;
  line-height: 1.25; /* 125% */
}

.label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;

  width: 112px;
  height: 95px;

  text-align: center;

  border-radius: 10px;
  border: 1px solid var(--border-color);

  transition: border-color var(--animation-cubicBezier);
}

.label:hover,
.label:focus {
  border: 1px solid var(--red-cl);
  cursor: pointer;
}

.label:has(input[type='checkbox']:checked) {
  border: 1px solid var(--red-cl);
}
.visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
